@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Space+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-color: #cddde2;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
main {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px 0;
}

.shadow {
  box-shadow: 10px 10px 13px -1px rgba(0, 0, 0, 0.09);
}
header {
  background-color: #5caac3;
  text-align: center;
  padding: 5px 10px;
}
header .brandIcon {
  display: inline;
  color: white;
  margin: auto;
  font-size: 2em;
}
header h1 {
  display: inline;
  margin: 0;
  padding: 0 15px;
  color: white;
  font-family: "Permanent Marker", cursive;
}
.frame {
  background-color: white;
  border-radius: 4px;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.petImage {
  flex: 1 1;
}
.petImage img {
  border-radius: 3px;
  padding: 10px;
  height: 50vh;
}

.profile {
  padding: 10px;
  flex: 1 1;
}
.profile h2 {
  margin: 4px 0;
}
.profile .desc p {
  display: inline;
  padding-right: 5px;
  font-style: italic;
}
.controls {
  flex: 1 1;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}
.controls button {
  font-size: 2em;
  border-radius: 50%;
  border-style: none;
  padding: 0.5em;
  color: white;
}
.controls .add {
  background-color: green;
}
.controls .more {
  color: grey;
}
.controls .pass {
  background-color: red;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.controls button:hover {
  cursor: pointer;
}
footer {
  color: grey;
  text-align: center;
  font-family: "Space Mono", monospace;
  font-size: 12px;
}
